import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';

//RenderRows
function RenderRows(props){
    //mapfor
    return props.posts.map(post => {
        return (
            <tr key={post.id}>
                <td>{post.id}</td>
                <td>{post.name}</td>
                <td><button className="btn btn-secondary" onClick={() => props.deleteTask(post)}></button></td>
            </tr>
        );
    });
}

export default class Example extends Component {

    constructor() {
        super();

        this.state = {
            posts: [],
            post: ''
        };
        this.inputChange = this.inputChange.bind(this);
        this.addPost = this.addPost.bind(this);
        this.deleteTask = this.deleteTask.bind(this);
    }

    componentDidMount() {
        axios.get('/api/posts')
            .then(response => {
                this.setState({posts: response.data});
            })
            .catch(() => {
                console.log(error);
            });
    }

    //
    inputChange(event){
        switch(event.target.name){
            case 'post':
                this.setState({
                    post: event.target.value
                });
                break;
            default:
                break;
        }
    }

    //
    addPost(){
        //
        if(this.state.post == ''){
            return;
        }
        //
        axios
            .post('/api/add', {
                name: this.state.post,
            })
            .then((response) => {
                //posts
                this.setState({
                    posts: response.data,
                    post: ''
                });
            })
            .catch(error => {
                console.log(error);
            });
    }

    //
    deleteTask(post){
        axios
            .post('/api/del', {
                id: post.id
            })
            .then((response) => {
                this.setState({
                    posts: response.data
                });
            })
            .catch(error => {
                console.log(error);
            });
    }

    render() {
        return (
            <div className="container">
                {/* add from */}
                <div className="form-group mt-4">
                    <label htmlFor="post">Post</label>
                    <input type="text" className="form-control" name="post" value={this.state.post} onChange={this.inputChange}/>
                </div>
                {/* table */}
                <button className="btn btn-primary" onClick={this.addPost}></button>
                <table>
                    <tbody>
                        <RenderRows
                            posts={this.state.posts}
                            deleteTask={this.deleteTask}
                        />
                    </tbody>
                </table>
            </div>
        );
    }
}

if (document.getElementById('example')) {
    ReactDOM.render(<Example />, document.getElementById('example'));
}
